import React from "react";
import "./ChartBar.css";

const ChartBar = (props) => {
  let barFillHeight = "0%";

  if (props.maxValue > 0) {
    barFillHeight = Math.round((props.value / props.maxValue) * 100) + "%";
  }
  // barFillHeight = Math.round gives us the percentage that how much height of the bar should be filled.

  return (
    <div className="chart-bar">
      <div className="chart-bar__inner">
        <div
          className="chart-bar__fill"
          style={{ height: barFillHeight }}
        ></div>
      </div>
      <div className="chart-bar__label">{props.label}</div>
    </div>
  );
};

export default ChartBar;

// 'style' takes javascript object that's why we have used double curly brackets.
// { height: barFillHeight } is a javascript object.
// if we want to use background color in style than we should wrap it in quotes or use camel case approach like backgroundColor:red.
