import React from "react";
import ChartBar from "./ChartBar";
import "./Chart.css";

const Chart = (props) => {
  const dataPointValues = props.dataPoints.map((dataPoint) => dataPoint.value);
  //we transform the datapoint object into a number.It will return the new array of numbers.
  // we will have 12 numbers in the array because we have 12 months in the dataPoints array.

  const totalMaximum = Math.max(...dataPointValues);
  // we have used spread operator to pull out the standalone numbers from the dataPointValues array.
  // totalMaximum is used to calculate the maximum number.

  return (
    <div className="chart">
      {props.dataPoints.map((dataPoint) => (
        <ChartBar
          key={dataPoint.label}
          value={dataPoint.value}
          maxValue={totalMaximum}
          label={dataPoint.label}
        />
      ))}
    </div>
  );
};

export default Chart;
